





































import Vue from 'vue'
import Component from 'vue-class-component'
import Tabs from '../components/Tabs.vue'
import intervalList from '@/constants/intervalList.ts'
import recordTypesList from '@/constants/recordTypesList.ts'
import dayjs from 'dayjs'
import clone from '@/lib/clone'
import Charts from '@/components/Charts.vue'
import _ from 'lodash'

@Component({
  components: { Tabs, Charts },
})
export default class Statistics extends Vue {
  created() {
    this.$store.commit('fetchRecords')
  }
  mounted() {
    window.scrollTo(0, 0)
    const chartDiv = this.$refs.ChartsWrapper as HTMLDivElement
    chartDiv.scrollLeft = chartDiv.scrollWidth
  }

  get recordList() {
    return this.$store.state.recordList
  }

  get groupedList() {
    const { recordList } = this

    const newList: RecordItem[] = clone(recordList as RecordItem[])
      .filter((r) => r.type === this.type)
      .sort(
        (a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
      )
    if (newList.length === 0) {
      return []
    }
    type Result = { title: string; total?: number; items: RecordItem[] }[]
    const result: Result = [
      {
        title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'),
        items: [newList[0]],
      },
    ]
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i]
      const last = result[result.length - 1]
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current)
      } else {
        result.push({
          title: dayjs(current.createdAt).format('YYYY-MM-DD'),
          items: [current],
        })
        console.log(current)
      }
    }
    result.map((group) => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0)
    })

    return result
  }

  get keyValueList() {
    const today = new Date()
    const array = []
    for (let i = 0; i <= 29; i++) {
      const dateString = dayjs(today)
        .subtract(i, 'day')
        .format('YYYY-MM-DD')
      const foundAmount = _.find(this.groupedList, {
        title: dateString,
      })
      array.push({
        date: dateString,
        value: foundAmount ? foundAmount.total : 0,
      })
    }

    array.sort((a, b) => {
      if (a.date > b.date) {
        return 1
      } else if (a.date === b.date) {
        return 0
      } else {
        return -1
      }
    })
    return array
  }

  get chartOptions() {
    const keys = this.keyValueList.map((item) => item.date)
    const value = this.keyValueList.map((item) => item.value)

    return {
      title: {
        text: '走势图',
        right: 16,
      },
      grid: {
        // 去掉echarts左右padding
        left: 0,
        right: 0,
      },
      tooltip: {
        show: true,
        triggerOn: 'click',
        formatter: '{c}',
        position: 'top',
        borderColor: 'none',
        backgroundColor: 'rgba(50,50,50,1)',
        textStyle: {
          color: '#fff',
        },
      },
      xAxis: {
        type: 'category',
        data: keys,
        aixsLine: {
          lineStyle: {
            color: '#666',
          },
        },
        axisTick: {
          alignWithLabel: true,
        },
        axisLabel: {
          formatter: function(value: string) {
            return value.substr(5)
          },
        },
      },
      yAxis: {
        type: 'value',
        show: false,
      },
      series: [
        {
          type: 'line',
          symbol: 'circle',
          itemStyle: {
            color: '#666',
            borderColor: '#666',
            borderWidth: 1,
          },
          data: value,
          symbolSize: 12,
        },
      ],
    }
  }

  beautify(string: string) {
    const day = dayjs(string)
    const now = dayjs()
    // isSame 是同样的
    if (day.isSame(now, 'day')) {
      return '今天'
      // subtract 减去
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天'
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天'
    } else if (day.isSame(now, 'year')) {
      return day.format('M月D日')
    } else {
      return day.format('YYYY年MM月DD日')
    }
  }
  tagString(Tags: Tag[]) {
    return Tags.length === 0 ? '无' : Tags.map((item) => item.name).join('，')
  }

  type = '-'
  recordTypesList = recordTypesList
}
